@mixin visually-hidden() {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  height: 1px;
  width: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
}

@mixin reset-list() {
  margin: 0;
  padding: 0;
  list-style: none;
}

@mixin reset-child-margins() {
  & > :first-child {
    margin-top: 0;
  }

  & > :last-child {
    margin-bottom: 0;
  }
}

@mixin angled-bg(@height: 25px, @top: 0) {
  &::before {
    content: "";
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    background: @grey-light url("../img/bg-triangle-white.svg") no-repeat center top;
    background-size: 100% @height;
    z-index: 0;
  }
}

@mixin container() {
  width: 320px;
  padding-left: 20px;
  padding-right: 20px;
  margin: 0 auto;

  @media (min-width: @tablet-width) {
    width: 660px;
  }

  @media (min-width: @desktop-width) {
    width: 960px;
    padding-right: 10px;
    padding-left: 10px;
  }
}

@mixin container-phones() {
  width: 320px;
  padding-left: @paddings;
  padding-right: @paddings;
  margin: 0 auto;
}
