@mixin visually-hidden() {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  height: 1px;
  width: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
}
@mixin reset-list() {
  margin: 0;
  padding: 0;
  list-style: none;
}
@mixin reset-child-margins() {
   > :first-child {
    margin-top: 0;
  }
   > :last-child {
    margin-bottom: 0;
  }
}
@mixin angled-bg(25px: 25px, 0: 0) {
  ::before {
    content: "";
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    background: #f2f2f2 url("../img/bg-triangle-white.svg") no-repeat center top;
    background-size: 100% 25px;
    z-index: 0;
  }
}
@mixin container() {
  width: 320px;
  padding-left: 20px;
  padding-right: 20px;
  margin: 0 auto;
  @media (min-width: 660px) {
    width: 660px;
  }
  @media (min-width: 960px) {
    width: 960px;
    padding-right: 10px;
    padding-left: 10px;
  }
}
@mixin container-phones() {
  width: 320px;
  padding-left: 0;
  padding-right: 0;
  margin: 0 auto;
}
@font-face {
