.main-nav__trigger {
  position: absolute;
  top: 21px;
  right: 19px;
  width: 50px;
  height: 24px;
  padding: 0;
  background-color: transparent;
  border: none;
  cursor: pointer;
  outline: none;
}
@media (min-width: 660px) {
  .main-nav__trigger {
    top: 44px;
  }
}
@media (min-width: 960px) {
  .main-nav__trigger {
    display: none;
  }
}
.main-nav--closed .main-nav__trigger span {
  display: block;
  width: 50px;
  height: 4px;
  background-color: #ffffff;
  border-radius: 1px;
}
.main-nav--closed .main-nav__trigger span::before,
.main-nav--closed .main-nav__trigger span::after {
  content: "";
  position: absolute;
  left: 0;
  width: 50px;
  height: 4px;
  background-color: #ffffff;
  border-radius: 1px;
}
.main-nav--closed .main-nav__trigger span::before {
  top: 0;
}
.main-nav--closed .main-nav__trigger span::after {
  bottom: 0;
}
.main-nav--opened .main-nav__trigger span::before,
.main-nav--opened .main-nav__trigger span::after {
  content: "";
  position: absolute;
  right: 0;
  top: 10px;
  width: 29px;
  height: 4px;
  background-color: #ffffff;
  border-radius: 1px;
}
.main-nav--opened .main-nav__trigger span::before {
  transform: rotate(45deg);
}
.main-nav--opened .main-nav__trigger span::after {
  transform: rotate(-45deg);
}
.main-nav__trigger:hover span,
.main-nav__trigger:hover span::before,
.main-nav__trigger:hover span::after {
  background-color: #d22856;
}
.main-nav__trigger:active span,
.main-nav__trigger:active span::before,
.main-nav__trigger:active span::after {
  background: rgba(210, 40, 86, 0.3);
}
@media (max-width: 959px) {
  .main-nav--closed .main-nav__items {
    display: none;
  }
  .main-nav--nojs .main-nav__trigger {
    display: none;
  }
  .main-nav--nojs .main-nav__items {
    display: block;
  }
}
