.btn--cancel:focus {
  opacity: 0.3;
}
.btn--cancel:active {
  background-color: #283645;
  color: #ffffff;
  opacity: 0.3;
}
.btn--download {
  border: 3px solid #ffffff;
  border-radius: 30px;
  color: #ffffff;
  background-color: transparent;
}
@media (min-width: 960px) {
