  }
}
.btn--download:hover,
.btn--download:focus {
  background-color: #ffffff;
  color: #d22856;
}
.btn--download:active {
  color: rgba(210, 40, 86, 0.3);
}
.btn--submit,
.btn--popup {
  padding: 17px 30px 16px;
  border-radius: 3px;
  font-size: 18px;
  line-height: 30px;
  text-transform: uppercase;
  color: #ffffff;
  background-color: #2aab6d;
}
.btn--submit:hover,
.btn--popup:hover,
.btn--submit:focus,
.btn--popup:focus {
  background-color: #1c9b5e;
}
.btn--submit:active,
.btn--popup:active {
  color: rgba(255, 255, 255, 0.3);
}
.btn--submit:disabled,
.btn--popup:disabled {
