.copyright {
  font-size: 0;
  margin-top: 40px;

  @media (min-width: @tablet-width) {
    margin-top: 14px;
    width: 150px;
    text-align: right;
  }

  @media (min-width: @tablet-width) {
    margin-top: 14px;
    width: 170px;
  }
}

.copyright__develop-txt {
  font-size: @font-size;
  display: inline-block;
  vertical-align: top;

  @media (min-width: @tablet-width) {
    padding-top: 3px;
  }

  @media (min-width: @desktop-width) {
    padding-top: 5px;
    font-size: 18px;
    line-height: 18px;
  }
}

.copyright__owner {
  display: inline-block;
  vertical-align: middle;
  margin-left: 14px;
  width: 27px;
  height: 34px;
}

.copyright__academy {
  fill: @pink;

  &:hover {
    fill: @blue-dark;
  }

  &:active {
    opacity: 0.3;
  }
}
